* {
  font: 1rem system-ui, Arial, sans-serif;
}

html {
  width: 100%;
  height: 100%;
  overflow: hidden;
  background: linear-gradient(
    90deg,
    rgb(192, 56, 255) 0%,
    rgb(83, 175, 255) 100%
  );
  background-color: rgb(240, 240, 240);
  background-repeat: no-repeat;
  background-size: auto 18rem;
  user-select: none;
}

body {
  width: 100%;
  max-width: 50rem;
  margin: 0 auto;
}

.app-container {
  margin: 0 2rem;
  height: calc(100vh - 7rem);
}

.header {
  font-size: 2.5rem;
  color: white;
  letter-spacing: 1rem;
  text-transform: uppercase;
  margin: 6rem 0 0;
}

.footer {
  text-align: center;
  vertical-align: middle;
  margin: 0;
  line-height: 5rem;
  color: rgb(200, 200, 200);
}

.new-task-form {
  margin-top: 2rem !important;
  background-color: white;
  border-radius: 0.2rem;
  box-shadow: 0px 0px 2rem 0.5rem rgba(0, 0, 0, 0.2);
}

.todo-list-container {
  margin-top: 2rem;
  background-color: white;
  border-radius: 0.2rem;
  box-shadow: 0px 0px 2rem 0.5rem rgba(0, 0, 0, 0.2);
}

.todo-list {
  list-style-type: none;
  padding: 0;
  margin: 0;
  max-height: calc(100vh - 25rem);
  overflow: overlay;
  overflow-x: hidden;
}

.todo-list::-webkit-scrollbar {
  width: 0.5rem;
  background: transparent;
}

.todo-list::-webkit-scrollbar-thumb {
  background-color: rgb(180, 180, 180);
  border-radius: 1rem;
}

.todo-list > *:has(li) {
  border-top: 1px solid rgba(0, 0, 0, 0.2);
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
}

.todo-list-footer {
  border-top: 1px solid rgba(0, 0, 0, 0.2);
  color: rgb(150, 150, 150);
}

.todo-list-footer {
  display: flex;
  height: 3rem;
  align-items: center;
  padding: 0 1rem;
}

.todo-list-footer > * {
  flex: 1;
}

.filters {
  justify-content: center;
  display: flex;
  column-gap: 1rem;
}

.actions {
  justify-content: right;
  display: flex;
}

.btn {
  background: none;
  border: none;
  padding: 0;
  cursor: pointer;
  color: inherit;
}

.btn:hover,
.btn[aria-pressed='true'] {
  color: rgb(0, 0, 0);
}

.btn[aria-pressed='true'] {
  color: rgb(0, 136, 255);
}

.todo-container {
  display: flex;
  height: 3rem;
  padding: 0 1rem;
  margin: 0;
  align-items: center;
}

.todo-container > .chk {
  width: 1.5rem;
  height: 1.5rem;
  background-color: white;
  border-radius: 50%;
  border: 2px solid #ddd;
  appearance: none;
  outline: none;
  cursor: pointer;
}

.todo-container > .chk:checked {
  background: url('data:image/svg+xml;base64,'),
    linear-gradient(315deg, rgb(192, 56, 255) 0%, rgb(83, 175, 255) 100%);
  background-size: 0.7rem, 2rem;
  background-repeat: no-repeat;
  background-position: center;
  border: none;
}

.todo-container > .chk:hover:not(:checked) {
  background: linear-gradient(rgb(255, 255, 255), rgb(255, 255, 255))
      padding-box,
    linear-gradient(315deg, rgb(192, 56, 255) 0%, rgb(83, 175, 255) 100%);
  border: 2px solid transparent;
}

.todo-container > .lbl {
  cursor: pointer;
  flex: 1;
  margin-left: 1rem;
  overflow: hidden;
  white-space: pre-wrap;
  text-overflow: ellipsis;
}

.todo-container:hover > .todo-actions > .btn {
  opacity: 0.2;
}

.todo-task-name {
  flex: 1;
  margin-left: 1rem;
  padding: 0;
  border: 0;
  outline: 0;
}

.lbl.crossout {
  text-decoration: line-through;
  color: rgb(180, 180, 180);
}

.edit-btn {
  background-image: url('data:image/svg+xml;base64,') !important;
}

.delete-btn {
  background-image: url('data:image/svg+xml;base64,');
}

.todo-actions > .btn:hover {
  opacity: 1 !important;
}

.todo-actions > .btn {
  margin-left: 1rem;
  background-repeat: no-repeat;
  background-position: 50% 50%;
  color: transparent !important;
  height: 1.2rem;
  width: 1.2rem;
  opacity: 0;
  outline: 0;
}

.dragging {
  border: 1px solid rgba(0, 0, 0, 0.2);
  background: rgb(240, 240, 240);
}

.hidden {
  visibility: hidden;
}

input[type="text"]::placeholder {
  font-style: italic;
}
